@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
html{scroll-behavior:smooth}body{font-family:"Roboto", sans-serif !important;color:#fff !important;margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.vertical-timeline::before{background:#000 !important}a,a:hover,a:focus,a:active{outline:none;color:#fff !important;text-decoration:none !important}.m0{margin:0 !important}.ppc-background{background-color:#000;background-image:linear-gradient(to top, #000 0%, darkgrey 100%)}.ppc-background__timeLine{background-color:#000}.ppc-hero{height:100vh;min-height:550px}.ppc-arrow_down{position:absolute;bottom:10px;right:10px}.ppc-tombstone{animation:shake 0.8s}.ppc-tombstone:hover{animation:shake 0.8s infinite}.ppc-timeline_title{margin-left:0.5rem}.ppc-timeline_text{font-size:1.5rem;font-style:italic}.ppc-timeline_source{font-size:0.8rem}@keyframes shake{0%{transform:translate(1px, 1px) rotate(0deg)}10%{transform:translate(-1px, -2px) rotate(-1deg)}20%{transform:translate(-3px, 0px) rotate(1deg)}30%{transform:translate(3px, 2px) rotate(0deg)}40%{transform:translate(1px, -1px) rotate(1deg)}50%{transform:translate(-1px, 2px) rotate(-1deg)}60%{transform:translate(-3px, 1px) rotate(0deg)}70%{transform:translate(3px, 1px) rotate(-1deg)}80%{transform:translate(-1px, -1px) rotate(1deg)}90%{transform:translate(1px, 2px) rotate(0deg)}100%{transform:translate(1px, -2px) rotate(-1deg)}}

